<template>
  <div class="todolist-content-box">
    <div
      class="list-item"
      v-for="(list, listIndex) in taskListData"
      :key="listIndex"
    >
      <!-- 每个列表顶部 -->
      <div class="list-tips-box">
        <div class="list-tips" :class="listIndex"></div>
        <div class="list-set" @click="openAllFn(listIndex)">
          {{ openAll[listIndex][0] ? '全部收起' : '全部展开' }}
        </div>
      </div>
      <!-- 列表盒子 -->
      <div class="list-box">
        <div class="list-box-layout">
          <transition-group name="todo-group">
            <div
              class="list-item-box"
              v-for="item in list"
              :key="item.matter_id"
            >
              <task-list-item
                :taskData="item"
                :openAll="openAll[listIndex][0]"
                :resetOneOpen="resetOneOpen"
                @deletedTaskList="deletedTaskList"
                @changeState="changeState"
                @myIsOpenChange="myIsOpenChange"
              ></task-list-item>
            </div>
          </transition-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import taskListItem from './taskListItem.vue';
import { STATE } from './config.js';
export default {
  props: {
    taskListData: {
      type: Object
    }
  },
  name: '',
  components: {
    taskListItem
  },
  setup(props, ctx) {
    const data = reactive({
      resetOneOpen: false, //改变单个为收起状态

      openAll: {} //全部状态展开收起
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 初始化openAll状态
    const initOpenState = () => {
      for (let key in STATE) {
        // [全部状态 , 当前展开个数]
        data.openAll[STATE[key]] = [false, 0];
      }
    };
    // 初始化
    initOpenState();
    // 全部展开收起
    // 1.切换全部状态取反
    // 2.设置全部展开数量
    const openAllFn = (index) => {
      const currentStateOpen = data.openAll[index]; //当前对应状态的全部展开收起状态
      const currentStateNum = data.openAll[index]; //保存单个展开状态的数量
      currentStateOpen[0] = !currentStateOpen[0]; //全部状态取反切换
      if (currentStateOpen[0]) {
        //转开为true 就保存全部数量
        currentStateNum[1] = props.taskListData[index].length;
      } else {
        //清空全部数量
        currentStateNum[1] = 0;
      }
    };
    // 接受子组件触，再发触发父组件
    const deletedTaskList = (task) => {
      ctx.emit('deletedTaskHome', task);
    };
    // 改变任务状态
    const changeState = (val) => {
      // 被移除的状态  ,被添加的状态
      const resetTaskList = [val[0].matter_state, val[1]];
      ctx.emit('changeState', val); //执行改变状态
      // 循环重置状态变化的列表全部重置收起
      // 被移除的状态列表全部收起，打开个数0
      // 被移入的状态列表全部收起，打开个数0
      resetTaskList.forEach((item) => {
        data.openAll[item][0] = false;
        data.openAll[item][1] = 0;
      });
      // 重置单独展开的所有元素
      data.resetOneOpen = !data.resetOneOpen;
    };
    // 改变单个展开状态  val [状态列表,展开状态]
    // 1.改变对应展开数量
    // 2.根据展开数量，判断改变全部展开收起状态
    const myIsOpenChange = (val) => {
      const currentStateNum = data.openAll[val[0]]; //对应状态的展开数量
      const allOpen = data.openAll[val[0]]; //状态对应全部状态
      if (val[1]) {
        //单个展开时true
        currentStateNum[1]++; //展开数量++
      } else {
        //单个收起时false
        currentStateNum[1]--; //展开数量--
      }
      // 当前展开数量等于数据列表长度，就切换全部展开状态
      if (currentStateNum[1] === props.taskListData[val[0]].length) {
        allOpen[0] = true;
      }
      // 当前展开数量0就，改为全部收起状态
      if (currentStateNum[1] === 0) {
        allOpen[0] = false;
      }
    };
    return {
      deletedTaskList,
      myIsOpenChange,
      changeState,
      openAllFn,
      ...toRefs(data)
    };
  }
};
</script>
