      overflow: hidden;
      background: #ffffff;
      border: 1px solid #efefef;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
      box-sizing: border-box;
      .list-tips {
        float: left;
        width: 12px;
        height: 12px;
        border-radius: 7px;
        &.notStarted {
          background: #ff0000;
        }
        &.conduct {
          background: #00bfff;
        }
        &.complete {
          background: #09ff00;
        }
      }
      .list-set {
        float: right;
        font-size: 12px;
