        cursor: pointer;
        color: #999;
      }
    }
    .list-box {
      padding-top: 25px;
      height: 100%;
      box-sizing: border-box;
      .list-box-layout {
        height: 100%;
        overflow: auto;

        /* 设置滚动条的样式 */
        &::-webkit-scrollbar {
          width: 3px;
        }
        /* 滚动槽 */
        &::-webkit-scrollbar-track {
          border-radius: 10px;
          background: rgb(224, 224, 224);
        }
        /* 滚动条滑块 */
        &::-webkit-scrollbar-thumb {
          border-radius: 10px;
          background: rgb(189, 189, 189);
        }
        .list-item-box {
          background: #ffffff;
          border: 1px solid #efefef;
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.06);
          box-sizing: border-box;
          border-radius: 4px;
          margin-bottom: 7px;
        }
      }
    }
  }
}
</style>
