<template>
  <div class="todolist-page">
    <!-- 左边部分收缩按键 -->
    <p
      class="iconfont icon-xiangzuoshouqi"
      :class="{ leftBoxSwitch }"
      @click="leftBoxSwitch = !leftBoxSwitch"
    ></p>
    <!-- 左侧大盒子 -->
    <div class="todolist-box-l" :class="{ leftBoxSwitch }">
      <div class="todolist-add-box">
        <!-- 右上角功能盒子 -->
        <div class="func-box">
          <p class="func-btn">导入</p>
          <p class="func-btn out">导出</p>
          <p class="func-btn empty-all" @click="emptyAllTaskList">
            清空全部任务列表
          </p>
        </div>
        <!-- 左侧添加任务盒子 -->
        <div class="add-box">
          <addTask @addTask="addTask"></addTask>
        </div>
      </div>
    </div>
    <!-- 右侧大盒子 -->
    <div class="todolist-box-R" :class="{ leftBoxSwitch }">
      <taskList
        :taskListData="taskListData"
        @deletedTaskHome="deletedTaskHome"
        @changeState="changeState"
      ></taskList>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { getLS, setLS, removeLS } from '@/utils/common.js';
import addTask from './addTask.vue';
import taskList from './taskList.vue';
import { STATE } from './config.js';
export default {
  name: '',
  components: {
    addTask,
    taskList
  },
  setup() {
    const data = reactive({
      leftBoxSwitch: false, //控制左侧显示隐藏
      taskListData: {} //保存本地全部任务数据
    });

    onBeforeMount(() => {});
    onMounted(() => {});
    // 获取全部本地数据
    const getAllTaskData = () => {
      for (let key in STATE) {
        data.taskListData[STATE[key]] = getLS(STATE[key]);
      }
      console.log(data.taskListData);
    };
    getAllTaskData();
    // 获取增加的任务信息
    const addTask = (task) => {
      const state = task.matter_state; //增加任务的状态
      data.taskListData[state].unshift(task); //增加对应状态的数据
      setLS(state, data.taskListData[state]); //再把对应更新状态的数据全部保存至本地
    };
    // 子组件触发的删除操作
    const deletedTaskHome = (task) => {
      const state = task.matter_state; //增加任务的状态
      const targetArr = data.taskListData[state]; //要操作的任务数组
      targetArr.forEach((item, index) => {
        //删除指定id任务
        if (item.matter_id === task.matter_id) {
          targetArr.splice(index, 1);
        }
      });
      setLS(state, data.taskListData[state]); //再把对应更新状态的数据全部保存至本地
    };
    // 改变任务状态
    const changeState = (val) => {
      const targetObj = val[0]; //操作对象
      const targetState = val[1]; //目标状态
      deletedTaskHome(targetObj); //先从原来状态数组中删除
      targetObj.matter_state = targetState; //改变操作对象的状态
      addTask(targetObj); //在添加到目标状态数组
    };
    // 清空全部任务列表
    const emptyAllTaskList = () => {
      // 循环清除本地任务列表
      for (let key in STATE) {
        removeLS(STATE[key]);
      }
      //清除后从新获取数据
      getAllTaskData();
    };
    return {
      addTask,
      getAllTaskData,
      deletedTaskHome,
      changeState,
      emptyAllTaskList,
      ...toRefs(data)
    };
  }
};
</script>
